$(function () {
    ajaxService.bannerList({pageSize: 10}, function (a) {
        if (0 == a.code) {
            let i = "";
            for (let e = 0; e < a.data.length; e++) {
                var t = a.data[e];
                i += `<a href="${t.link_to || "javascript:;"}" target="${t.link_to ? "_blank" : ""}" class="${t.link_to ? "item swiper-slide" : "swiper-slide item default"}">
           <span class="img" data-url="${t.image}" style="background: url('${t.image}') no-repeat center;filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='${t.image}',sizingMethod='scale')"></span>
           </a>`
            }
            $(".swiper-wrapper").html(i), 8 == tool.myBrowser() && $(".swiper-wrapper .item").each(function () {
                var e = $(this).children(".img").data("url");
                $(this).children(".img").css("filter", `progid:DXImageTransform.Microsoft.AlphaImageLoader(src='${e}',sizingMethod='scale')`)
            });
            var e = new Swiper(".swiper-container", {
                autoplay: 1 < a.data.length.length && 5e3,
                speed: 500,
                loop: !0,
                calculateHeight: !0,
                autoplayDisableOnInteraction: !1,
                pagination: ".pagination",
                paginationClickable: !0,
                swipeToNext: !1,
                swipeToPrev: !1
            });
            $(".swiper-container .prev").click(function () {
                e.swipePrev()
            }), $(".swiper-container .next").click(function () {
                e.swipeNext()
            })
        }
    }), ajaxService.indexNews({num: 4, lang: "zh-CN"}, function (a) {
        if (0 == a.code) if (a.data.length < 2) $(".news-wrap").remove(); else {
            let i = "";
            for (let e = 0; e < a.data.length; e++) {
                var t = a.data[e], n = t.brief;
                n && 57 < n.length && (n = n.slice(0, 57) + "...");
                var s = t.update_at && t.update_at.split(" ")[0].substr(5, 5);
                i += `<a class="swiper-slide news-item ${e % 2 == 0 ? "fl" : "fr"}" target=_blank href="/cn/about_newsdetail.html?id=${t.id}">
            <div class="news-item-img"><image src="${t.image}" /></div>
            <div class="news-item-text"><h4 title="${t.title}">${t.title}</h4><p class="news-item-brief">${n}</p>
            </div> <span class="news-item-date">${s}</span></a>`
            }
            $(".news-main .swiper-wrapper").html(i), 8 == tool.myBrowser() && $(".news-main .news-item").each(function () {
                var e = $(this).find("h4"), i = e.text();
                24 < i.length && e.text(i.slice(0, 24) + "...")
            });
            var e = new Swiper(".news-main-wrap", {
                autoplay: 5e3,
                speed: 500,
                loop: !0,
                calculateHeight: !0,
                autoplayDisableOnInteraction: !1,
                swipeToNext: !1,
                swipeToPrev: !1,
                slidesPerView: 2,
                slidesPerGroup: 2
            });
            $(".news-main .prev .arrow").click(function () {
                e.swipePrev()
            }), $(".news-main .next .arrow").click(function () {
                e.swipeNext()
            })
        }
    })
});